








































import { Component, Vue, Prop, Emit } from "vue-property-decorator";
import { disasterServer, gisServer } from "@/api/installServer";

interface opt {
  // sceneId: string | number;
  // accidentRoad: [] | string | object;
  [key: string]: any;
}

@Component({
  name: "Optimization",
  components: {},
})
export default class Optimization extends Vue {
  private Opt: opt = {
    modelName: "", //
    level: 5,
    rainfall: 40,
    startTime: "",
  };

  @Prop() private defaultData?: any;

  private options: object[] = [
    {
      value: "快速评估法",
      label: "快速评估法",
    },
    {
      value: "Risk_assessment",
      label: "危险性评价",
    },
    {
      value: "瑞典圆弧法",
      label: "瑞典圆弧法",
    },
    {
      value: "滑坡临界预警",
      label: "滑坡临界预警",
    },
    {
      value: "毕晓普法",
      label: "毕晓普法",
    },
  ];

  private mounted() {
    if (this.defaultData) {
      this.Opt = this.defaultData;
    }
  }

  // 取消
  private empty() {
    (this.$refs["form"] as any).resetFields();
  }

  // 提交
  private submit() {
    let opts: any = {
      rainfall: this.Opt.rainfall,
      level: this.Opt.level,
      model: this.Opt.modelName,
      //time: this.Opt.startTime
    };
    console.log(opts);
    this.$Bus.$emit(
      "setCenter",
      { longitude: 108.47215331, latitude: 33.32226317 },
      13
    );
    this.$Bus.$emit("getHuapo");
  }
  // 返回
  private goback() {
    let data: any = {
      data: {},
      index: 1,
    };
    this.setIndex(data);
  }

  @Emit("setPanelView")
  private setIndex(data: any) {
    return data;
  }
}
