





























































import { Component, Vue, Prop, Emit } from "vue-property-decorator";
@Component({
  name: "Optimization",
  components: {},
})
export default class Optimization extends Vue {
  private Opt: any = {
    starttime: "", // 发生时间
    Duration: "", // 模拟时长
    disasterType: "", // 原生灾害
    disasterCoordinate: "", // 灾害坐标
    SourceCoordinate: "", // 震源坐标
    SourceLevel: "", // 地震等级
    fractureDirection: "", //破裂方向
    landslideLevel: "", // 滑坡灾害等级
    precipitation: "", // 降水量
    buildingCollapse: "", // 建筑垮塌
    fire: "",
  };

  @Prop() private defaultData?: any;

  private mounted() {
    if (this.defaultData) {
      this.Opt = this.defaultData;
    }
  }

  // 取消
  private empty() {
    (this.$refs["form"] as any).resetFields();
  }

  // 提交
  private submit() {
    let data: any = {
      data: {},
      index: -1,
    };
    this.setIndex(data);
  }
  // 返回
  private goback() {
    let data: any = {
      data: {
        Optimization: this.Opt,
      },
      index: 3,
    };
    this.setIndex(data);
  }

  @Emit("setPanelView")
  private setIndex(data: any) {
    return data;
  }
}
