






















import { Component, Vue, Prop, Watch } from "vue-property-decorator";

import gisMain from "@/components/common/GisMain.vue";
import DisStat from "@/views/theme/fireassembly/DisasterStatistics.vue"; //echart图展示
import extract from "@/views/theme/fireassembly/extract.vue"; // 应急语义提取
import chain from "@/views/theme/fireassembly/chain.vue"; // 逻辑链
@Component({
  name: "fireView",
  components: {
    gisMain,
    DisStat,
    extract,
    chain
  },
})
export default class fireView extends Vue {
  private rightTopIndex: any = 1;
  private data: any = {};
  private setPanelView(res: any) {
    console.log(res, "数据");
    this.data = { ...this.data, ...res.data };
    this.rightTopIndex = res.index;
  }
}
