
















import { Component, Vue, Prop, Watch } from "vue-property-decorator";

import gisMain from "@/components/common/GisMain.vue";
import floodModel from "@/views/theme/water-logging/floodModel.vue";
import Optimization from "@/views/theme/water-logging/Optimization.vue";
@Component({
  name: "waterLoggingView",
  components: {
    gisMain,
    Optimization,
    floodModel,
  },
})
export default class waterLoggingView extends Vue {
  public showlinecharts: any = false;
  private rightTopIndex: any = 1;
  private data: any = {};
  private echartData: any = {};
  private setPanelView(res: any) {
    console.log(res, "数据");
    this.data = { ...this.data, ...res.data };
    this.rightTopIndex = res.index;
  }
  mounted() {
    this.$Bus.$on("floodModel", (data) => {
      this.showlinecharts = true;
      this.echartData = data.item;
    });
    this.$Bus.$on("close", () => {
      this.showlinecharts = false;
    });
  }
}
